let sexArr = ['保密', '男', '女'];
let job = ['保密', 'IT', '老师'];
let qgArr = ['保密', '未婚', '已婚'];
import mpvueCityPicker from "../../components/mpvue-citypicker/mpvueCityPicker.vue";
import Time from "../../common/time.js"
export default {
	name: "user-set-userinfo",
	components: {
		mpvueCityPicker
	},
	data() {
		return {
			sexArr: sexArr,
			qgArr: qgArr,
			userpic: "",
			username: "",
			sex: 0,
			job: "",
			qg: 0,
			birthday: "",
			cityPickerValueDefault: [0, 0, 1],
			pickerText: ""

		}
	},
	onLoad() {
		this.userpic = this.User.userinfo.userpic;
		this.username = this.User.userinfo.username;
		this.sex = this.User.userinfo.userinfo.sex || 0;
		this.qg = this.User.userinfo.userinfo.qg || 0;
		this.job = this.User.userinfo.userinfo.job || "请填写";
		this.birthday = this.User.userinfo.userinfo.birthday || "请填写";
		this.pickerText = this.User.userinfo.userinfo.path || "请填写";
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	methods: {
		async submit() {
			let data = {
				name: this.username,
				sex: this.sex,
				qg: this.qg,
				job: this.job,
				birthday: this.birthday,
				path: this.pickerText,
				age: Time.gettime.getAgeByBirthday(this.birthday)
			}
			//上传信息
			let [err,res] = await this.$http.post('/edituserinfo',data,{token:true,checkToken:true});
			//失败
			if(!this.$http.errorCheck(err,res)) return;
			// 成功
			uni.showToast({ title: '修改成功！' });
			// 修改状态，缓存
			this.User.userinfo.username = this.username;
			this.User.userinfo.userinfo = data;
			uni.setStorageSync('userinfo',this.User.userinfo);
		},
		// 修改家乡 三级联动选择
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show()
		},
		// 修改生日
		bindDateChange: function(e) {
			this.birthday = e.target.value
		},
		// 修改头像
		async changeimg() {
			let [err,res] = await uni.chooseImage({
				count: 1, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
			})
			if(!res) return;
			//上传
			uni.showLoading({title: '上传中',mask: false});
			let [err2,res2] = await this.$http.upload('/edituserpic',{
				name: 'userpic',
				filePath: res.tempFilePaths[0],
				token:true,
				checkToken:true
			});
			let data = JSON.parse(res2.data);
			//上传失败
			if(err2 || data.errorCode){
				uni.showToast({
					title: data.msg ?	 data.msg : '上传失败',
					icon: 'none'
				})
				uni.hideLoading();
				return false;
			}
			//成功
			uni.hideLoading();
			uni.showToast({ title: '修改头像成功!' });
			this.userpic = data.data;
			//修改状态 储存
			this.User.userinfo.userpic = this.userpic;
			uni.setStorageSync('userinfo',this.User.userinfo);
			
		},
		// 修改性别 工作 情感
		changeOne(type) {
			let arr;
			switch (type) {
				case 'sex':
					arr = sexArr;
					break;
				case 'job':
					arr = job;
					break;
				case 'qg':
					arr = qgArr;
					break;
				default:
					break;
			}
			uni.showActionSheet({
				itemList: arr,
				success: res => {
					switch (type) {
						case 'sex':
							this.sex = res.tapIndex;
							break;
						case 'job':
							this.job = arr[res.tapIndex];
							break;
						case 'qg':
							this.qg = res.tapIndex;
							break;
						default:
							break;
					}
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		onConfirm(e) {
			this.pickerText = e.label;
		}
	},
}
